"use client";
import { useState } from "react";
import { Proveedor } from "../../dashboard/proovedor/page";
import { FiEdit2, FiTrash2, FiPackage } from "react-icons/fi";

interface Props {
  proveedores: Proveedor[];
  onEliminar: (id: number) => void;
  onEditar: (id: number, proveedor: Omit<Proveedor, "idProveedor">) => void;
}

export default function ProveedorTable({ proveedores, onEliminar, onEditar }: Props) {
  const [editandoId, setEditandoId] = useState<number | null>(null);
  const [formData, setFormData] = useState<Omit<Proveedor, "idProveedor">>({
    ruc: "",
    razonSocial: "",
    direccion: "",
    telefono: "",
    paginaWeb: "",
    email: "",
  });

  const iniciarEdicion = (prov: Proveedor) => {
    setEditandoId(prov.idProveedor);
    setFormData({ ...prov });
  };

  const guardarCambios = () => {
    if (editandoId !== null) {
      onEditar(editandoId, formData);
      setEditandoId(null);
    }
  };

  return (
    <div>
      <h2 className="subtitulo">
        <FiPackage /> Lista de Proveedores
      </h2>
      <table className="tabla-empresas">
        <thead>
          <tr>
            <th>RUC</th>
            <th>Razón Social</th>
            <th>Teléfono</th>
            <th>Email</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {proveedores.map((p) => (
            <tr key={p.idProveedor}>
              <td>
                {editandoId === p.idProveedor ? (
                  <input value={formData.ruc} onChange={(e) => setFormData({ ...formData, ruc: e.target.value })} />
                ) : (
                  p.ruc
                )}
              </td>
              <td>
                {editandoId === p.idProveedor ? (
                  <input value={formData.razonSocial} onChange={(e) => setFormData({ ...formData, razonSocial: e.target.value })} />
                ) : (
                  p.razonSocial
                )}
              </td>
              <td>
                {editandoId === p.idProveedor ? (
                  <input value={formData.telefono} onChange={(e) => setFormData({ ...formData, telefono: e.target.value })} />
                ) : (
                  p.telefono
                )}
              </td>
              <td>
                {editandoId === p.idProveedor ? (
                  <input value={formData.email} onChange={(e) => setFormData({ ...formData, email: e.target.value })} />
                ) : (
                  p.email
                )}
              </td>
              <td className="flex gap-2">
                {editandoId === p.idProveedor ? (
                  <button onClick={guardarCambios} className="boton-accion boton-agregar">
                    Guardar
                  </button>
                ) : (
                  <button onClick={() => iniciarEdicion(p)} className="boton-accion boton-editar">
                    <FiEdit2 /> Editar
                  </button>
                )}
                <button onClick={() => onEliminar(p.idProveedor)} className="boton-accion boton-eliminar">
                  <FiTrash2 /> Eliminar
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
