"use client";
import { useState } from "react";
import { FiPlus, FiX } from "react-icons/fi";
import { Button } from "../../../store/ui/button";

export interface Proveedor {
  idProveedor: number;
  ruc: string;
  razonSocial: string;
  direccion: string;
  telefono: string;
  paginaWeb: string;
  email: string;
}

interface Props {
  onAgregar: (proveedor: Omit<Proveedor, "idProveedor">) => void;
  onCancelar: () => void;
}

export default function ProveedorForm({ onAgregar, onCancelar }: Props) {
  const [formData, setFormData] = useState<Omit<Proveedor, "idProveedor">>({
    ruc: "",
    razonSocial: "",
    direccion: "",
    telefono: "",
    paginaWeb: "",
    email: "",
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!formData.ruc.trim() || !formData.razonSocial.trim()) return;
    onAgregar(formData);
  };

  return (
    <form
      onSubmit={handleSubmit}
      className="space-y-6 overflow-y-auto max-h-[70vh] px-1"
    >
      <div className="grid grid-cols-1 gap-4">
        {[
          {
            name: "ruc",
            label: "N° RUC *",
            placeholder: "Ingrese RUC del Proveedor",
          },
          {
            name: "razonSocial",
            label: "Razón Social *",
            placeholder: "Ingrese Razón Social del Proveedor",
          },
          {
            name: "direccion",
            label: "Dirección",
            placeholder: "Ingrese Dirección del Proveedor",
          },
          {
            name: "telefono",
            label: "Teléfono",
            placeholder: "Ingrese Teléfono del Proveedor",
          },
          {
            name: "paginaWeb",
            label: "Página Web",
            placeholder: "Ingrese Página Web del Proveedor",
          },
          {
            name: "email",
            label: "E-Mail",
            placeholder: "Ingrese Email del Proveedor",
          },
        ].map((f) => (
          <div key={f.name} className="flex flex-col">
            <label
              htmlFor={f.name}
              className="mb-1 text-sm font-medium text-gray-700"
            >
              {f.label}
            </label>
            <input
              id={f.name}
              name={f.name}
              placeholder={f.placeholder}
              value={(formData as any)[f.name]}
              onChange={handleChange}
              required={f.name === "ruc" || f.name === "razonSocial"}
              className="w-full rounded-md border border-gray-300 px-3 py-2 text-sm shadow-sm focus:border-black focus:outline-none focus:ring-0"
            />
          </div>
        ))}
      </div>

      <div className="flex justify-end gap-2">
        <Button type="submit" className="flex items-center gap-2">
          <FiPlus /> Guardar
        </Button>
        <Button
          type="button"
          variant="secondary"
          className="flex items-center gap-2"
          onClick={onCancelar}
        >
          <FiX /> Cerrar
        </Button>
      </div>
    </form>
  );
}